/* webfont import */
@font-face {
  font-family: 'Cafe24Ssurround';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/Cafe24Ssurround.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}



*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

html{
  min-height:100%;
  background-color: #f0f0f0;
}

.App {
  text-align: center;
  background-color: #f0f0f0;
}

/* Header style */
.App-header {
  background-color: #f0f0f0;
  /* width:90%; */
}
.navBarPC{
  /* border:1px solid red; */
  background-color: #f0f0f0;
  width:90%;
  margin: 30px auto;
  font-family: 'Cafe24Ssurround', 'Courier New', monospace;
  font-stretch:extra-expanded;
}
.navContainer{

}
.logo{
  width:90px;
  display: inline-block;

  /* drag none */
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  /* user-drag: none; */
}
.navBoxPC{
  margin-right:30px;
}
.navItem{
  color:#8f8f8f;
}
.navItem:hover{
  color:#405980;
}

/* main style*/
.main{
  width:90%;
  height:700px;
  /* border:1px solid blue; */
  background-color: #f9f9f9;
  margin: 150px auto;
  padding: 50px;
  position:relative;
  border-radius: 15px;
  
}
.circleProfile{
  width:330px;
  height:330px;
  /* border:1px solid purple; */
  border-radius: 50%;
  position:absolute;
  top:-100px;
  left:5%;
  border:15px solid white;
  /* box-shadow: 10px 10px 1.5px #888e98; */
}
.profilePic{
  width:100%;
  height:100%;
  border-radius: 50%;

  /* drag none */
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
}





